body {
  background: #fff;
  padding: 0 5px;
  margin: 0;
  font-family: "Roboto"; }

* {
  box-sizing: border-box; }

.chart__window path, .chart__window text, .chart__map path {
  transition: .4s ease-in-out;
  transform: translateZ(0); }

.chart__window .dragging path {
  transition: 0.08s ease-in-out; }

.chart__window .dragging text {
  transition: 0.3s ease-in-out; }

.chart_wrapper {
  position: relative;
  margin: 30px auto 0;
  width: 100%;
  max-width: 600px; }

.chart__window {
  position: relative;
  width: 100%;
  height: 350px; }

.chart__map {
  height: 100px;
  margin: 10px 0;
  position: relative; }

.chart__controls {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }

.chart__control-start {
  width: 10px;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #000;
  z-index: 1;
  user-select: none; }

.chart__control-start:before {
  content: '';
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  right: 100%;
  background-color: rgba(51, 163, 218, 0.5);
  pointer-events: none; }

.chart__control-end {
  width: 10px;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  background-color: #000;
  z-index: 1;
  user-select: none; }

.chart__control-end:before {
  content: '';
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  left: 100%;
  background-color: rgba(51, 163, 218, 0.5);
  pointer-events: none; }

.line-y0, .line-y1 {
  position: relative;
  z-index: 2; }

svg, g {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #fff; }

text {
  opacity: 0;
  transition: 0.3s ease !important;
  user-select: none; }

.active-item {
  opacity: 1 !important; }

.removing-item {
  opacity: 0; }

.tooltip-text {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.25);
  border-radius: 7px;
  padding: 10px 0px 0px 15px;
  max-width: 280px; }

.tooltip-date {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 500;
  margin-right: 15px;
  white-space: nowrap; }

.tooltip-values-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.tooltip-value-wrapper {
  margin: 0 15px 10px 0;
  flex-grow: 1;
  width: calc(50% - 15px); }

.tooltip-value {
  font-size: 17px;
  font-weight: bold;
  display: block; }

.tooltip-value-name {
  font-size: 15px;
  display: block; }

.values-wrapper path {
  opacity: 0;
  transition: .05s ease !important; }
